.formTitle {
  color: #2665C4;
  font-size: 18px;
  text-align: left; }

.textinput {
  border-top: 0px;
  border-right: 0px;
  border-left: 0.5px solid #2665C4;
  border-bottom: 0.5px solid #2665C4;
  border-radius: 5px;
  padding: 5px; }

.input-container {
  display: inline-block;
  margin: 14px;
  clear: both; }

.label {
  color: #2665C4;
  position: absolute;
  margin-top: -9px;
  margin-left: 10px;
  font-size: 14px; }

.submit {
  margin: 0px;
  padding: 3px 18px 3px 18px;
  cursor: pointer;
  background-color: white;
  border-radius: 5px;
  border-color: #2665C4;
  border-style: solid;
  border-width: 2px; }

.buttondiv {
  clear: both;
  margin-left: 30px; }
